      .sidebar-overlay {
        opacity: 0.8;
        visibility: visible;
      }
    }
    .sidebar-overlay {
      visibility: hidden;
      opacity: 0;
      background-color: #212331;
      position: fixed;
      z-index: 6;
      top: 0;
      left: 0;
