      direction: ltr;
      text-align: left;
      li {
        position: relative;
        display: block;
        margin: 0;
        padding: 0;
        direction: ltr;
        text-align: left;
        float: none;
        a {
          padding: 10px 50px 20px 40px;
          position: relative;
          color: #222222;
          font-size: 14px;
          font-weight: 400;
          line-height: 23px;
          text-decoration: none;
          text-transform: uppercase;
          letter-spacing: 0.07em;
          display: block;
        }
        ul {
          position: absolute;
          width: 200px;
          left: 100%;
          top: 0;
          padding: 15px 0px;
          display: none;
          background-color: #fff;
          li {
            a {
              padding: 5px 35px;
              &:active,
              &:focus,
              &.highlighted {
                padding: 5px 35px;
              }
            }
          }
        }
        .mega-menu {
          position: absolute;
          left: 100%;
          width: 815px !important;
          padding: 15px 30px 25px !important;
          background-color: #ffffff;
          box-shadow: 0 0 1px 0 #ebebeb;
          li {
            ul {
              position: relative;
              width: 100%;
              left: inherit;
            }
          }
        }
      }
    }
    .pixelstrap {
      li {
        a {
          padding: 10px 50px 20px 40px;
        }
        ul {
          li {
            a {
              padding: 5px 35px;
              &:active,
              &:focus,
              &.highlighted {
                padding: 5px 35px;
              }
            }
          }
        }
        .clothing-menu {
          li {
            a {
              padding: 5px 0 !important;
              &:focus {
                font-size: 14px;
              }
              &:hover {
                font-size: 14px;
                padding: 5px 0;
              }
              &:focus,
              &:hover {
                padding: 5px 0 !important;
              }
            }
          }
        }
      }
    }
  }
  .layout3-menu {
    .main-navbar {
      .nav-menu {
        display: inline-block;
        list-style: none;
        margin: 0;
        padding: 0;
        line-height: normal;
        direction: ltr;
        text-align: left;
        > li {
          position: relative;
          float: left;
          display: block;
          > a {
            text-transform: capitalize;
            padding-right: 34px;
            letter-spacing: 0.04em;
          }
          &.mega-menu {
            position: unset;
          }
        }
      }
    }
  }
}
