              color: $theme-deafult;
              font-size: 14px;
              padding: 5px 35px;
            }
          }
        }
      }
    }
    .category-menu {
      a {
        &:hover {
          img {
            transform: scale(1);
          }
        }
      }
    }
    &.sm-vertical {
      a {
        .sub-arrow {
          margin-top: -18px;
        }
      }
    }
    .mega {
      ul {
        max-width: 100%;
      }
    }
    ul {
      li {
        border-top: none;
      }
    }
  }
  .sidenav {
    overflow-y: auto;
    .pixelstrap {
      li {
        ul {
          li {
            a {
              padding: 5px 45px;
              &:hover,
              &.highlighted {
                padding: 5px 45px;
              }
            }
          }
        }
        a {
          padding: 10px 25px;
          &:hover {
            padding: 10px 25px;
          }
        }
      }
    }
  }
  .main-menu {
    &.border-section {
      border: none;
    }
    .menu-left {
      .main-menu-right {
        .toggle-nav {
          position: absolute;
          z-index: 99;
          right: -20vw;
          top: 0vh;
          .sidebar-name {
            font-size: 18px;
            padding-left: 5px;
            text-transform: uppercase;
          }
        }
      }
    }
  }
  .main-nav-center {
    .toggle-nav {
      position: relative;
      z-index: 99;
    }
  }
  .layout3-menu {
    .main-menu {
      .menu-left {
        .main-menu-right {
          .toggle-nav {
            right: unset;
            top: -1px;
          }
        }
      }
    }
  }
  .rtl {
    .mega-box {
      .link-section {
        .demo {
          ul {
            li {
              a {
                span {
                  img {
                    left: -70px;
                  }
                }
              }
            }
          }
        }
      }
    }
    .sm {
      li {
        text-align: right;
      }
    }
    .pixelstrap {
      a {
        .sub-arrow {
          right: unset;
          left: 0;
        }
      }
      li {
        .lable-nav {
          left: unset;
          right: 15px;
          &:before {
            left: unset;
            right: 5px;
          }
        }
      }
    }
  }
}
@media (max-width: 991px) {
  body,
  html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow-y: scroll;
    overflow-x: hidden !important;
  }
  .top-header {
    .header-contact {
      display: none;
    }
  }
}
@media (min-width: 578px) {
  .main-menu {
    .menu-right {
      .icon-nav {
        .mobile-search,
        .mobile-cart,
        .mobile-setting {
          i {
            display: none;
          }
          .close-circle {
            i {
              display: block;
            }
          }
        }
        .mobile-search {
          .show-div {
            .form-inline {
              button {
                i {
                  display: inline-block;
                }
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 577px) {
  .top-header {
    height: 0;
  }
  .main-nav-center {
    .toggle-nav {
      right: 0vw;
      top: 0vh;
      margin-top: 2vh;
      padding-top: 0%;
      padding-bottom: 0%;
    }
  }
  .mobile-fix-option {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 20vh;
    background-color: $font-color;
    width: 100%;
    z-index: 9;
    box-shadow: 0 2px 5px 0 rgba(168, 168, 168, 1);
  }
  .header-5 {
    .top-header {
      .header-dropdown {
        .mobile-wishlist,
        .mobile-account {
          img {
            filter: brightness(5);
          }
        }
      }
    }
    .main-menu {
      .brand-logo {
        margin: 0;
        left: 0;
        transform: unset;
      }
    }
  }
  .top-header {
    .header-dropdown {
      .mobile-account,
      .mobile-wishlist {
        position: fixed;
        bottom: 12%;
        right: 80%;
        font-size: 0;
        padding: 0;
        z-index: 9;
        i {
          font-size: 22px;
          font-family: $font-themify;
          color: $header-font;
          &:before {
            content: "\e602";
          }
        }
        .onhover-show-div {
          bottom: 6vh;
          right: 2vw;
          z-index: 2;
        }
        &:hover {
          i {
            color: $header-font;
          }
        }
      }
      .mobile-account {
        right: 35%;
        bottom: 14%;
      }
      .mobile-setting {
        bottom: 13.6%;
      }
      .mobile-wishlist {
        right: 80%;
        i {
          font-family: $font-themify;
          color: $header-font !important;
          &:before {
            content: "\e634";
          }
        }
      }
    }
  }
  .main-menu {
    .menu-left {
      &.category-nav-right {
        .navbar {
          padding-left: 0 !important;
        }
      }
      .navbar {
        padding: 30px 45px 30px 0 !important;
      }
    }
    .menu-right {
      .icon-nav {
        .mobile-setting,
        .mobile-cart,
        .mobile-search {
          position: fixed;
          bottom: 13.7%;
          right: 13%;
          font-size: 0;
          padding: 0;
          .show-div {
            top: -58px;
            left: 0;
            &.shopping-cart {
              top: unset;
              bottom: 42px;
              left: -118px !important;
            }
          }
        }
        .mobile-setting {
          i {
            display: inline-block;
            font-size: 22px;
            color: $header-font;
          }
          > div {
            img {
              display: none;
            }
          }
          .setting {
            top: unset;
            bottom: 42px;
            left: unset !important;
          }
        }
        .mobile-cart {
          right: 60%;
          .close-circle {
            i {
              font-size: 14px;
            }
          }
          i {
            display: inline-block;
            font-size: 22px;
            color: $header-font;
          }
          > div {
            img {
              display: none;
            }
          }
        }
        .mobile-search {
          width: 80%;
          position: fixed;
          bottom: 5%;
          right: 9.5%;
          font-size: 0;
          padding: 0;
          z-index: 1;
          i {
            display: inline-block;
            font-size: 22px;
            color: $header-font;
          }
          img {
            display: none;
          }
          .show-div {
            .form-group {
              margin-bottom: 0;
            }
            .form-inline {
              button {
                i {
                  font-size: 14px;
                }
              }
            }
          }
        }
      }
    }
  }
  .brand-logo {
    position: absolute;
    text-align: center;
    left: 50%;
    margin-right: -50%;
    transform: translateX(-50%);
    padding-top: 12vh !important;
    padding-bottom: 30px !important;
  }
  .toggle-nav {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .rtl {
    .main-nav-center {
      .toggle-nav {
        right: unset;
        left: 15px;
      }
    }
    .main-menu {
      .menu-right {
        .icon-nav {
          .mobile-setting,
          .mobile-cart,
          .mobile-search {
            right: unset;
            left: 16%;
            .show-div {
              left: unset;
              right: 0;
              &.shopping-cart {
                left: unset !important;
                right: -118px !important;
              }
            }
          }
          .mobile-setting {
            right: 13%;
            .setting {
              left: unset !important;
            }
          }
          .mobile-cart {
            right: unset;
            left: 40%;
          }
          .mobile-search {
            right: unset;
            left: 80%;
          }
        }
      }
      .menu-left {
        .navbar {
          padding: 30px 0 30px 25px !important;
        }
      }
    }
    .top-header {
      .header-dropdown {
        .mobile-account,
        .mobile-wishlist {
          right: unset;
          left: 32%;
          .onhover-show-div {
            right: 0;
          }
        }
        .mobile-wishlist {
          right: unset;
          left: 64%;
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .container-navbar {
    max-width: 90% !important;
    min-height: 10vh;
    margin-left: auto;
    margin-right: auto;
  }
  .brand-logo {
    img {
      height: 25px;
    }
  }
}
