@media (max-width: 1430px) {
  .new-product-img {
    min-width: 110px !important;
    max-width: 110px !important;
    min-height: 100px !important;
    max-height: 100px !important;
  }
  .navbar-container {
    padding-left: 2% !important;
  }
  .border-box .total li span {
    width: 60%;
  }
  .banner-slider .height-banner {
    height: unset;
  }
  .pet-parallax {
    .pet-decor {
      left: 60px;
      bottom: -125px;
      img {
        width: 240px;
      }
    }
  }
  // inner pages //
  .collection-product-wrapper {
    .product-top-filter {
      .product-filter-content {
        .search-count {
          width: 100%;
          border-left: 1px solid $round-border;
          border-bottom: 1px solid $round-border;
          text-align: center;
        }
        .collection-view {
          width: 20%;
          padding-right: 0 !important;
          border-left: 1px solid $round-border;
        }
        .collection-grid-view {
          padding: 20px !important;
          padding-left: 0 !important;
          width: 20%;
          ul {
            li {
              &:last-child {
                display: none;
              }
            }
          }
        }
        .product-page-filter {
          border-right: 1px solid $round-border;
          width: 60%;
        }
        .product-page-per-view {
          width: 30%;
        }
      }
      .popup-filter {
        .collection-view {
          width: 10%;
        }
        .product-page-per-view {
          width: 25%;
        }
      }
    }
  }
  .cart-section {
    .cart-buttons {
      > div {
        &:last-child {
          padding-right: 38px;
        }
      }
    }
    tbody {
      tr {
        td {
          min-width: 175px;
          .qty-box {
            .input-group {
              .form-control {
                width: 75px;
              }
            }
          }
        }
      }
    }
    tfoot {
      tr {
        td {
          padding-right: 41px;
        }
      }
    }
  }
  .product-right {
    .product-icon {
      .product-social {
        li {
          padding-right: 10px;
        }
      }
    }
  }
  .product-form-box {
    .timer {
      padding-left: 17px;
      span {
        width: 55px;
      }
    }
  }
  // rtl section //
  .rtl {
    .cart-section,
    .wishlist-section {
      .cart-buttons {
        > div {
          &:last-child {
            padding-left: 41px;
          }
        }
      }
    }
    .collection-product-wrapper {
      .product-filter-content {
        .collection-view {
          border-left: none;
          border-right: 1px solid $round-border;
        }
        .product-page-filter {
          border-left: 1px solid $round-border;
          border-right: none;
        }
      }
      .product-top-filter {
        .product-filter-content {
          .search-count {
            border-right: 1px solid $round-border;
            padding-right: 20px;
            h5 {
              text-align: center;
            }
          }
        }
      }
    }
    .product-right {
      .product-icon {
        .product-social {
          li {
            padding-left: 10px;
          }
        }
      }
    }
    header {
      &.left-header {
        .sidenav {
          right: -300px;
          left: unset;
          &.open-side {
            right: 0;
            left: unset;
          }
        }
        .top-header {
          padding-right: 0;
        }
      }
    }
    .left-sidebar_space {
      padding-right: 0;
      padding-left: 0;
    }
  }
  // dark //
  .dark {
    .collection-product-wrapper {
      .product-top-filter {
        .product-filter-content {
          .collection-view {
            border-color: $dark-border;
          }
          .product-page-filter {
            border-color: $dark-border;
          }
        }
      }
    }
  }
}

@media (max-width: 1430px) and (min-width: 1368px) {
  .navbar-container {
    padding-left: 2% !important;
  }
  .banner-timer {
    .timer {
      margin-top: 45px;
