}

@media (max-width: 1430px) and (min-width: 1200px) {
  .product-box {
    min-width: 300px;
    max-width: 300px;
    min-height: 250px;
    max-height: 250px;
    margin-top: 50px;
  }
  .img-collection-list {
    min-width: 265px;
    max-width: 265px;
    min-height: 200px;
    max-height: 200px;
  }

  .navbar-container {
    padding-left: 2% !important;
  }

  .layout3-menu {
    max-width: 100%;
    .main-menu {
      .menu-left {
        .navbar {
          padding: 40px 10px 40px 0;
        }
      }
    }
    .pixelstrap {
      > a {
        padding-right: 38px;
      }
    }
  }
  .portfolio-section {
    &.metro-section {
      .product-box {
        .cart-wrap {
          a {
            i {
              padding: 5px;
            }
          }
          i {
            font-size: 14px;
          }
          button {
            i {
              padding: 5px;
            }
          }
        }
      }
    }
  }
  // rtl section //
  .rtl {
    .layout3-menu {
      .main-menu {
        .menu-left {
          .navbar {
            padding: 40px 0 40px 35px;
          }
        }
      }
    }
  }
}

